window.$ = window.jQuery = function (selectorOrArray) {
    let elements
    if (typeof selectorOrArray === 'string') {
        elements = document.querySelectorAll(selectorOrArray)
    } else if (selectorOrArray instanceof Array) {
        elements = selectorOrArray
    }

    return {
        find(selector) {
            let array = []
            this.each((node) => {
                array.push(...node.querySelectorAll(selector))
            })
            array.oldApi = this  //this就是api  (api1)
            return jQuery(array)
        },
        each(fn) {
            for (let i = 0; i < elements.length; i++) {
                fn.call(null, elements[i], i)
            }
        },
        parent() {
            const array = []
            this.each((node) => {
                if (array.indexOf(node.parentNode) === -1) {
                    array.push(node.parentNode)
                }
            })
            return jQuery(array)
        },
        children() {
            const array = []
            this.each((node, i) => {
                // console.log(...node.children, `children${i}`)
                array.push(...node.children)
            })
            return jQuery(array)
        },
        siblings() {
            let array = []
            this.each((node) => {
                let c = Array.from(node.parentNode.children)
                array = array.concat(c.filter(n => n !== node))
            })
            return jQuery(array)
        },
        index(selector) {
            let index
            if (selector) {
                this.each((node, i) => {
                    if (node === document.querySelector(selector)) {
                        index = i
                    }
                })
            } else {
                let children = [...elements[0].parentNode.children]
                index = children.indexOf(elements[0])
            }

            return index
        },


        print() {
            console.log(elements)
        },
        addClass(className) {
            this.each((node) => node.classList.add(className))
            return this
        },

        oldApi: selectorOrArray.oldApi,
        end() {
            return this.oldApi   //this是当前api(api2)
        },
    }
}







